.card-depoimento {
  width: 100%;
  max-width: 38.4rem;
  height: 20.9rem;
  background-color: rgba(241, 240, 245, 0.55);
  border-radius: 8px;
  padding: 3.7rem 3.2rem 2.3rem 3.2rem;
  .user {
    @include flex(row, start, space-between);
    strong {
      font-family: "Poppins" sans-serif;
      font-size: 1.8rem;
      margin-bottom: 2.1rem;
      line-height: 112%;
      letter-spacing: -1px;
      color: $gray-500;
    }
  }
  p {
    width: 100%;
    font-weight: 400;
    max-width: 32.2rem;
    font-size: 1.8rem;
    padding-bottom: 2.3rem;
    line-height: 150%;
    color: $gray-400;
  }
}
